import _ from 'lodash';
import * as d3 from 'd3';
import markdown from 'markdown';
const md = markdown.markdown;
import leftpad from 'leftpad';

let container,
    controls,
    config_cache,
    data_cache,
    explanation_cache,
    x_scale,
    y_scale;

const container_id = '#decision-visualisation';
const width = 500, height = 200;
const margin = { left: 80, top: 50, right: 30, bottom: 50 };
const dispatch = d3.dispatch('toggle');

const init = () => {
  dispatch.on('toggle', function (c, i) {
    let selected_toggles = document.querySelector('.control input:checked');

    draw(data_cache, selected_toggles);
    metrics(data_cache, selected_toggles);

    if (explanation_cache) {
      d3.select(container_id + ' .explanation .subtext-title').text(explanation_cache.columns[i+1]);
      d3.select(container_id + ' .explanation .subtext').text(explanation_cache[0][c]);
      let img = d3.select(container_id + ' .explanation .image');
      img.html('');
      if (i > 0) {
        img.append('img')
           .attr('src', `${config_cache.explanation_pic_prefix}${leftpad(i, 3)}${config_cache.explanation_pic_postfix}`);
      }
    }
  });

  d3.text(config_cache.general_intro_path, text => {
    d3.select(container_id + ' .description')
      .append('p').html(md.toHTML(text));
  });

  d3.select('#decision-title').text(config_cache.project_name);
  controls = d3.select(container_id + ' .control');
  container = d3.select(container_id + ' .chart')
                .append('svg')
                .attr('width', width + margin.left + margin.right)
                .attr('height', height + margin.top + margin.bottom)
                .append('g').attr('class', 'chart-area')
                .attr('transform', `translate(${margin.left},${margin.top})`);

  d3.csv(config_cache.data_path, prepare, data => {
    data_cache = data;
    create_controls(data);
    [x_scale, y_scale] = render_axes(data);
    let selected_toggles = document.querySelector('.control input:checked');
    draw(data_cache, selected_toggles);
    metrics(data_cache, selected_toggles);
  });

  d3.csv(config_cache.explanation_path, explanation => {
    explanation_cache = explanation;
    // Trigger the first specific explanation text and pic to load
    dispatch.apply('toggle', this, [explanation_cache.columns[2], 1]);
  });
};

const prepare = row => {
  row = _.mapValues(row, v => Number(v));
  if (config_cache.x_label.toLowerCase() === 'year' && row[config_cache.x_label]) {
    row = { year: new Date(row[config_cache.x_label], 0, 1), ...row };
    delete row[config_cache.x_label];
  }
  return row;
};

const draw = (data, selected) => {
  if (!data) return;

  let selected_column = _.concat(selected.value, config_cache.constant_limit);
  let lines = container.selectAll('path.line')
                       .data(selected_column, d => d);

  lines.enter()
       .append('path').attr('class', c => {
          if (c === config_cache.constant_limit) {
            return 'line highlight';
          } else {
            return 'line';
          }
        })
       .attr('d', c => {
          return d3.line()
                   .x(d => x_scale(d.year))
                   .y(d => y_scale(d[c]))
                   (data);
        });

  lines.exit().remove();

  let areas = container.selectAll('path.area')
                       .data(selected_column, d => d);

  areas.enter()
       .append('path').attr('class', 'area')
       .attr('d', c => {
         return d3.area()
                  .x (d => x_scale(d.year))
                  .y0(d => y_scale(d[config_cache.constant_limit]))
                  .y1(d => y_scale(Math.max(d[config_cache.constant_limit], d[c])))
                  (data);

       });
  areas.exit().remove();
};

const metrics = (data, selected) => {
  if (!data) return;

  if (config_cache.project_type === 'albatross') {
    let selected_column = selected.value;
    let limit = _.map(data, d => d[config_cache.constant_limit]);

    // Metric #1: the number of years equal to or above the limit line
    let selected_data = _.map(data, d => d[selected_column] );
    let above_the_line = _.filter(selected_data, (d, i) => d > limit[i] );

    // Metric #2: the number of birds total above the limit line
    let extra_birds = _.sum(_.map(selected_data, (d, i) => Math.max(d - limit[i], 0)));
    let extra_birds_per_year = above_the_line.length > 0 ? Math.round(extra_birds / above_the_line.length) : 0;
    d3.selectAll(container_id + ' .metric p').remove();
    d3.select(container_id + ' .metric').append('p')
             .html(`In the <strong>${selected_column}</strong> scenario, our model
                    predicts the Albatross population will have
                    <span class="highlight">${above_the_line.length.toLocaleString()}</span>
                    years above the Management Reference Level.`);
    d3.select(container_id + ' .metric').append('p')
             .html(`In this path, there are <span class="highlight">${extra_birds_per_year.toLocaleString()}</span>
                    breeding pairs above the nominated Management Reference
                    Level.`);
  }
};

const create_controls = data => {
  let columns = _.remove(data.columns, c => c !== config_cache.x_label);
  columns.forEach((c, i) => {
    if (i === 0) return; // Skip the first column that is limit
    let control = controls.append('input')
                          .attr('type', 'radio')
                          .attr('name', 'visualisation-option')
                          .attr('id', `visualisation-option-${i}`)
                          .attr('value', c)
                          .on('click', function () { dispatch.apply('toggle', this, [c, i]); });
    controls.append('label')
            .attr('class', 'toggle')
            .attr('for', `visualisation-option-${i}`)
            .text(c);
    if (i === 1) {
      control.attr('checked', 'checked'); // Precheck first option
    }
  });
};

const render_axes = data => {
  let x_extent = d3.extent(data, d => d.year);
  let x_scale = d3.scaleTime()
                  .domain(x_extent)
                  .range([0, width])
                  .nice(d3.timeYear, 10);
  let x_axis = d3.axisBottom(x_scale)
                 .ticks(d3.timeYear.every(20));

  let y_extent = [0, 10000];
  let y_scale = d3.scaleLinear()
                  .domain(y_extent)
                  .range([height, 0]);
  let y_axis = d3.axisLeft(y_scale)
                 .ticks(10);

  // Render grids
  // http://stackoverflow.com/questions/15580300/proper-way-to-draw-gridlines
  let x_grid = d3.axisTop(x_scale)
                 .ticks(d3.timeYear.every(20))
                 .tickSize(height)
                 .tickFormat('');

  let y_grid = d3.axisRight(y_scale)
                 .ticks(10)
                 .tickSize(width+10)
                 .tickFormat('');

  container.append('g').attr('class', 'x grid')
           .attr('transform', `translate(0,${height})`)
           .call(x_grid);

  container.append('g').attr('class', 'y grid')
           .attr('transform', `translate(-10,0)`)
           .call(y_grid);

  // Render axes
  container.append('g').attr('class', 'x axis')
                       .attr('transform', `translate(0,${height})`)
                       .call(x_axis)
                       .append('text')
                       .attr('x', width/2)
                       .attr('y', 30)
                       .text(config_cache.x_label);

  container.append('g').attr('class', 'y axis')
           .attr('transform', `translate(-10,0)`)
           .call(y_axis)
           .append('text')
           .attr('x', 0)
           .attr('y', -15)
           .text(config_cache.y_label);

  return [x_scale, y_scale];
};

d3.json('data/config.json', config => {
  config_cache = config;
  init();
});